html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  background-image: url("/build/img/background.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 15%;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Titillium Web", sans-serif;
  margin: 3.5rem 0;
  text-align: center;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.container {
  width: 90%;
  margin: 0 auto;
  z-index: 11;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.no-click {
  pointer-events: none;
}

.margin-bottom {
  margin-bottom: 5rem;
}

.margin-top {
  margin-top: 5rem;
}

.no-border {
  border: none;
}

.no-display {
  display: none !important;
}

.header {
  background-color: #000000;
  position: relative;
}
.header .topbar {
  display: flex;
}
@media (min-width: 920px) {
  .header .topbar {
    justify-content: center;
    align-items: center;
    transform: translateX(-3%);
  }
}
.header .topbar img {
  height: 7.5rem;
  width: auto;
  margin-right: 5rem;
}
@media (min-width: 920px) {
  .header .topbar img {
    height: 8.5rem;
  }
}
.header .topbar .mobileMenu {
  background-color: #3D3D3D;
  display: grid;
  place-items: center;
  font-size: 3rem;
  color: #FFFFFF;
  height: 7.5rem;
  width: 7.5rem;
}
@media (min-width: 920px) {
  .header .topbar .mobileMenu {
    display: none;
  }
}
.header .topbar .mobileMenu:hover {
  cursor: pointer;
}
.header .topbar .navbar {
  display: none;
}
@media (min-width: 920px) {
  .header .topbar .navbar {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.header .topbar .navbar.show {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
  height: auto;
  position: absolute;
  top: 7.5rem;
  background-color: #000000;
  width: 100%;
  padding: 0 2rem;
}
.header .topbar .navbar.show a {
  margin: 1rem 0;
}
.header .topbar .navbar a {
  font-family: "Titillium Web", sans-serif;
  text-transform: uppercase;
  color: #FFFFFF;
  font-size: 1.6rem;
  margin-right: 1.4rem;
  font-weight: 700;
}
.header .topbar .navbar a:last-of-type {
  margin-right: 0;
}

.mainHeader {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 920px) {
  .mainHeader {
    grid-template-columns: 1fr 4fr 1fr;
    align-content: center;
    justify-items: center;
  }
}
.mainHeader .mainTitle {
  display: flex;
  justify-content: center;
  align-items: center;
}
.mainHeader .mainTitle h1 {
  font-size: 2.4rem;
  font-family: "Titillium Web", sans-serif;
  text-transform: uppercase;
  position: relative;
}
.mainHeader .mainTitle h1::after {
  content: "";
  position: absolute;
  display: block;
  width: 10rem;
  height: 0.4rem;
  background-color: #F44337;
  left: 34%;
}
@media (min-width: 920px) {
  .mainHeader .mainTitle h1 {
    font-size: 4.4rem;
  }
}
.mainHeader .mainTitle img {
  width: 4.4rem;
  height: fit-content;
  margin-right: 1.2rem;
}
.mainHeader button {
  background-color: transparent;
  grid-row: 2/3;
  text-align: right;
  text-transform: capitalize;
}
.mainHeader button:hover {
  cursor: pointer;
}
@media (min-width: 920px) {
  .mainHeader button {
    grid-row: 1/2;
    text-align: left;
  }
}
.mainHeader .viewChanger {
  display: none;
}
.mainHeader .viewChanger:hover {
  cursor: pointer;
}
.mainHeader .viewChanger p {
  margin-right: 1.2rem;
  text-transform: capitalize;
}
@media (min-width: 920px) {
  .mainHeader .viewChanger {
    display: flex;
    align-items: center;
  }
}
.mainHeader .viewChanger, .mainHeader button {
  text-decoration: underline;
  font-weight: 600;
  color: #4a4a4a;
  font-size: 1.6rem;
}

.switch {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 5rem;
  height: 2.5rem;
  padding: 3px;
  background-color: #828282;
  border-radius: 1.8rem;
  cursor: pointer;
}
.switch .switch-input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.switch .switch-handle {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 18px;
  height: 18px;
  background: white;
  border-radius: 10px;
  -webkit-transition: left 0.15s ease-out;
  -moz-transition: left 0.15s ease-out;
  -o-transition: left 0.15s ease-out;
  transition: left 0.15s ease-out;
}
.switch .switch-input:checked ~ .switch-handle {
  left: 55%;
}

.baseElement {
  margin-top: 2.5rem;
}
.baseElement .siteBlock {
  margin: 2.5rem 0;
  width: 100%;
  padding: 0 0 0 2rem;
  background-color: #FFFFFF;
  border-radius: 1.8rem;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
}
@media (min-width: 920px) {
  .baseElement .siteBlock {
    padding: 1.6rem 2.4rem;
  }
}
.baseElement .siteInfo {
  display: flex;
  align-items: center;
}
.baseElement .siteInfo .siteContent {
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .siteContent {
    gap: 2.5rem;
  }
}
.baseElement .siteInfo .siteContent img {
  width: 10rem;
  height: fit-content;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .siteContent img {
    width: 12rem;
  }
}
.baseElement .siteInfo .siteContent .siteId {
  font-size: 2.4rem;
  color: #3D3D3D;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .siteContent .siteId {
    font-size: 2.8rem;
  }
}
.baseElement .siteInfo .siteContent .review {
  color: #3D3D3D;
  text-decoration: underline;
  font-size: 1.6rem;
  text-align: center;
  display: block;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .siteContent .review {
    font-size: 2rem;
    text-align: left;
  }
}
.baseElement .siteInfo .siteContent .stars {
  display: none;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .siteContent .stars {
    display: flex;
  }
  .baseElement .siteInfo .siteContent .stars i {
    color: #FBC02D;
    font-size: 1.4rem;
    padding: 0.5rem;
  }
}
.baseElement .siteInfo .promo {
  font-size: 1.6rem;
  color: #2A42ED;
  overflow-wrap: break-word;
  text-align: center;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .promo {
    font-size: 2.2rem;
    text-align: left;
  }
}
.baseElement .siteInfo .playNow {
  font-size: 0rem;
  padding: 2rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color: #F44337;
  color: #FFFFFF;
  border-radius: 0 1.8rem 1.8rem 0;
  text-transform: capitalize;
  min-height: 4.3rem;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .playNow {
    border-radius: 0.4rem;
    font-size: 1.6rem;
    padding: 1rem 4rem;
    min-height: auto;
    inline-size: max-content;
  }
}
.baseElement .siteInfo .playNow i {
  margin-left: 0;
  color: #FFFFFF;
  font-size: 2rem;
}
@media (min-width: 920px) {
  .baseElement .siteInfo .playNow i {
    margin-left: 2.4rem;
    font-size: 1.6rem;
  }
}
.baseElement .mainBlock:hover > .siteBlock {
  transform: scale(1.05);
}
@media (min-width: 920px) {
  .baseElement .mainBlock:hover > .siteBlock {
    transform: scale(1.1);
  }
}

.sitesList {
  display: block;
}
.sitesList .siteBlock {
  gap: 1rem;
  align-items: center;
  justify-content: space-between;
  min-height: 8.3rem;
}
@media (min-width: 920px) {
  .sitesList .siteBlock {
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .sitesList .siteBlock {
    gap: 0rem;
  }
}
.sitesList .siteInfo {
  gap: 1.5rem;
  justify-content: space-around;
}
@media (min-width: 1024px) {
  .sitesList .siteInfo {
    gap: 7rem;
  }
}
@media (min-width: 1024px) {
  .sitesList .siteInfo {
    gap: 7rem;
  }
}
.sitesList .siteInfo .siteContent {
  justify-content: space-between;
}

@media (min-width: 920px) {
  .sitesCards {
    display: grid;
    gap: 1.6rem;
    grid-template-columns: repeat(5, 1fr);
  }
  @keyframes delay {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  .sitesCards .siteBlock {
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
  }
  .sitesCards .siteInfo {
    flex-direction: column;
    gap: 2.5rem;
    justify-content: space-evenly;
  }
  .sitesCards .siteInfo .siteContent {
    flex-direction: column;
    justify-content: space-evenly;
  }
  .sitesCards .siteInfo .siteContent .siteId {
    text-align: left;
    width: 100%;
    transform: translateX(-5rem);
  }
  .sitesCards .siteInfo .promo {
    text-align: center;
  }
  .sitesCards .siteInfo .playNow i {
    margin-left: 2.4rem;
  }
}